body {
  margin: 0;
  padding: 0;
}

label{
    display: block;
}

h2, p{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;  
}

:focus { outline: none; }

.navigation {
  list-style: none;
  margin: 0; 
  
  background: deepskyblue;
  display: -ms-flexbox;
  display: flex;
  
  -ms-flex-pack: start;
  
      justify-content: flex-start;
}

.navigation h1 {
  text-decoration: overline underline;
  display: block;   
  color: white;
  font-size: 1.3em;
  margin-left: 1%;
  margin-right: 1%;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.navigation-link {
    background-color: deepskyblue;
    border: none;
    padding-left: 1%;
    padding-right: 1%;
    color: white;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
}

.navigation-link a {
    text-decoration: none;
    color: white;
}

.navigation-link a:focus, .navigation-link a:hover { 
    text-decoration: underline;
    -webkit-text-decoration-color: white;
            text-decoration-color: white; 
}

.form-element {
    display: inline-block;
    margin: 1%;
}

.slc, input[type=number]{
   padding: 0.5%;
   -webkit-box-shadow: none;
           box-shadow: none;
   background-color: white; 
   border: 2px solid deepskyblue;
   border-radius: 5%;
   -webkit-transition-duration: 0.2s; /* Safari */
    -o-transition-duration: 0.2s;
      transition-duration: 0.2s;
}

.slc:hover, .slc:focus {
    background-color: deepskyblue;
    color: white;
}

.btn, .slc, label, input[type=number]{
    padding: 1% 1.5%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.btn {
    text-align: center;
    text-decoration: none;
    font-size: 1em;
    -webkit-transition-duration: 0.2s; /* Safari */
    -o-transition-duration: 0.2s;
       transition-duration: 0.2s;
    cursor: pointer;
    background-color: white; 
    color: black; 
    border: 2px solid #4CAF50;
    border-radius: 10%;
}

.btn:hover, .btn:focus {
    background-color: #4CAF50;
    color: white;
}

.chart-container-larger {
    width: 50%;
    display: inline-block;
}

.chart-container-large {
    width: 33%;
    display: inline-block;
}

.chart-container-medium {
    width: 25%;
    display: inline-block;
}

.chart-container-small {
    width: 16.6%;
    display: inline-block;
}

@media only screen and (max-width: 550px) {
    .chart-container-small, .chart-container-medium, .chart-container-large, 
    .chart-container-larger {
        width: 100%;
    }
}
.error-container {
    width: 100%;
    margin: auto;
    background-color: lightcoral;
}

.error-content {
    position: relative;
    text-align: center;
    color: white;
}


.loader-container {
    width: 100%;
    margin: auto;
}

.loader-content p {
    position: relative;
    text-align: center;
}
/*
    Spinner load
 */
.loader {
    visibility: visible;
    z-index: 100;
    position: relative;
    margin: auto;
    border: 7px solid #f3f3f3; /* Cinza claro */
    border-top: 7px solid deepskyblue; /* Azul */
    border-radius: 50%;
    width: 25px;
    height: 25px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}









